.flex-container {
  display: flex;
  flex-direction: row;
}

body {
  background-image : linear-gradient(to right, rgb(122, 248, 122), rgb(200, 255, 0));
  align-content: center;
}

h1 {
  padding-top: 15px;
}

.container {
  background-color: white;
  margin: auto;
  width: 50%;
  margin-top: 90px;
  box-shadow: 0 3px 8px rgba(0,0,0,0.25);
}

#task-container {
  width: 90%;
  margin:auto;
}

.form-wrapper{
  width: 50%;
  margin:auto;
  width: 100%;
}

#form {
  width: 100%;
  margin: auto;
}

.center {
  width: 90%;
  margin:auto;
}

#task-container {
  margin-left:auto;
  margin-right:auto;
  padding-top: 20px;
  padding-bottom: 25px;
}

#list-wrapper {
  width: 100%;
  height: 250px;
  margin: auto;
  margin-top: 20px;
  overflow: auto;
  size: fixed;
}

.task-wrapper{
  cursor: pointer;
  color: #686868;
  margin:5px;
  padding:10px;
}

.flex-wrapper {
  display: flex;
}

#item {
  border-bottom: 1px solid  #e9e9e9;
  color: #686868;
}

#item:hover {
  background-color: #F0F0F0;
}

.spinner {
  width: 60px;
  height:55px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}